.card-container {
    display: flex;
    flex-direction: column;
    background-color: #95dada;
    border: 1px solid grey;
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    transform: translate();
    transition: 0.25s ease-out;
    text-align: center;
}

.card-container:hover{
    transform: scale(1.05);
}

